import { writable } from "svelte/store";
class AboutStore {
    constructor() {
        this.write = writable([]);
        this.update = this.write.update;
    }
    async fetchData() {
        const req = await fetch("data/about.json");
        const data = await req.json();
        this.write.set(data);
    }
    get subscribe() {
        return this.write.subscribe;
    }
    async load() {
        this.update((d) => {
            if (!d.length)
                this.fetchData();
            return d;
        });
        return void 0;
    }
}
export const store = new AboutStore();
